/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #000000;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.section-start {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .section-start {
    display: flex;
    justify-content: flex-start;
    margin: 1rem;
  }
}
.section-start .section-name {
  font-size: 2.5rem;
  color: #DB4444;
}

.rectangle {
  display: inline-block;
  width: 1.5rem;
  height: auto;
  background-color: #DB4444;
  border-radius: 0.5rem;
  margin: 2rem 1.5rem 2rem 0;
}

@media (min-width: 768px) {
  .list {
    display: flex;
    margin-left: 2rem;
    gap: 2.5rem;
  }
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #000000;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #000000;
  font-size: 2rem;
}

.header {
  display: flex;
  flex-direction: column;
  margin: 2.5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    flex-direction: row;
  }
}
.header .company-name {
  text-transform: uppercase;
  font-family: "Lilita One", cursive;
  font-size: 3rem;
  color: #000000;
}
.header .navigation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  padding: 0 5rem;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.header .navigation.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 768px) {
  .header .navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    opacity: 1;
    visibility: visible;
  }
}
.header .navigation .header-links {
  gap: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .header .navigation .header-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    padding-right: 5rem;
  }
}
.header .navigation .header-links a {
  color: #000000;
  display: inline-flex;
  align-items: center;
}
.header .navigation .header-links a:hover {
  border-bottom: 3px solid #000000;
}
.header .navigation .header-account {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .header .navigation .header-account {
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0 0 auto;
  }
}
.header .icon {
  font-size: 3rem;
  color: #000000;
}

.mobile-menu {
  font-size: 3rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.search-bar {
  margin: 0 2rem;
  margin-bottom: 2rem;
  border: 1px solid #000000;
  border-radius: 3rem;
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
}
.search-bar .search-input {
  flex: 1;
  display: flex;
}
.search-bar .search-input input {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 0;
  outline: none;
}
.search-bar .search-input input:focus {
  outline: none;
}
.search-bar .search-icon {
  background-color: #DB4444;
  border: none;
  color: #FFFFFF;
  border-radius: 3rem;
  padding: 0 2rem;
  justify-content: flex-end;
}

.footer {
  background-color: #000000;
  display: grid;
  gap: 2.5rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: auto 1fr;
  margin-top: auto;
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .logo {
    grid-column: 1/2;
  }
}
.logo .logo-image {
  width: 15rem;
  height: auto;
  margin: 0;
}
.logo .logo-name {
  margin: 0;
  text-transform: uppercase;
  color: #FFFFFF;
  font-family: "Lilita One", cursive;
}

.footer-links {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.footer-links .link-section {
  color: #F3A5A5;
  font-weight: 700;
  margin: 0;
}
.footer-links a {
  color: #FFFFFF;
}

.products__list {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  align-items: center;
  margin: 2rem 5rem;
}
@media (min-width: 768px) {
  .products__list {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: min(100%, 20rem);
}
.product__image {
  background-color: #F5F5F5;
  padding: 3rem;
  border-radius: 2rem;
}
.product__name {
  margin: 0;
  text-align: center;
}
.product__price {
  text-align: center;
  font-weight: 700;
  color: #DB4444;
  margin: 0;
}

.detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 2rem;
}

.product-details {
  margin: 2rem;
}
@media (min-width: 768px) {
  .product-details {
    display: flex;
    margin: 5rem;
  }
}
.product-details .product-images {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.product-details .product-images .main-image {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  width: min(100%, 40rem);
}
.product-details .product-images .small-image {
  display: flex;
  justify-content: center;
}
.product-details .product-images .small-image img {
  padding: 0.5rem;
  width: 5rem;
  height: auto;
  border: 1px solid #000000;
}
.product-details .product-info {
  display: flex;
  flex-direction: column;
  margin: 0 5rem;
}
.product-details .product-info .info-name {
  font-weight: 700;
  font-size: 2.5rem;
}
.product-details .product-info .info-price {
  margin: 0;
  font-weight: 700;
  font-size: 2rem;
  color: #DB4444;
}
.product-details .product-info .info-price span {
  display: block;
  margin: 1rem 0;
  color: #000000;
}
.product-details .product-info .info-description {
  font-size: 1.5rem;
  margin: 1rem 0;
}
.product-details .shipping {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2rem 5rem;
}
@media (min-width: 768px) {
  .product-details .shipping {
    padding-top: 0;
    width: 50%;
    margin: 0 5rem;
  }
}
.product-details .shipping form {
  border-radius: 3rem;
  border: 1px solid #000000;
  padding: 2rem;
}
.product-details .shipping__address {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #000000;
}
.product-details .shipping__address i {
  font-size: 4rem;
}
.product-details .shipping__details {
  border-bottom: 1px solid #000000;
}
.product-details .shipping__details p span {
  color: #DB4444;
}
.product-details .shipping__details .delivery {
  color: #3CBD58;
  font-weight: 700;
}
.product-details .shipping__details .date span {
  font-weight: bold;
  color: #000000;
}
.product-details .shipping .quantity {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.product-details .shipping .quantity input {
  width: 8rem;
  border-radius: 5rem;
  padding: 1rem 0.5rem 1rem 2rem;
}
.product-details .shipping .add {
  background-color: #DB4444;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-right: 1.5rem;
  border-radius: 5rem;
}
@media (min-width: 768px) {
  .product-details .shipping .add {
    width: auto;
  }
}
.product-details .shipping .add:hover {
  background-color: #c62626;
  cursor: pointer;
}

.login-container {
  display: flex;
  flex-direction: column;
  margin: 0 5rem;
}
@media (min-width: 768px) {
  .login-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: 2.5rem 5rem;
  }
}
.login-container .image-login {
  width: min(50rem, 100%);
  height: auto;
}
.login-container .login-form {
  margin: 5rem auto;
  width: 100%;
}
@media (min-width: 768px) {
  .login-container .login-form {
    margin: auto 5rem;
    width: 40%;
  }
}

.profile {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 0 auto 5rem auto;
}
.profile .title {
  color: #DB4444;
}
.profile .save-changes {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.profile .save-changes a {
  color: #000000;
}
@media (min-width: 768px) {
  .profile .save-changes {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .horizontal {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .horizontal .form__field {
    width: 45%;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  margin: auto 5rem;
}
@media (min-width: 768px) {
  .contact {
    display: flex;
    flex-direction: row;
  }
  .contact .contact-info {
    width: 30%;
  }
}
.contact .contact-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contact .contact-info {
    align-items: flex-start;
    margin-right: 2rem;
  }
}
.contact .contact-info .title-info {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.contact .contact-info .title-info .icon-contact {
  margin-right: 1rem;
  background-color: #DB4444;
  padding: 1rem;
  border-radius: 5rem;
}
.contact .contact-info .info-description {
  text-align: center;
}
.contact .contact-info .info-description p {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contact .contact-info .info-description {
    text-align: left;
  }
}

.contact-form {
  width: 100%;
}
.contact-form .form__field {
  width: 100%;
  margin: 0;
}
@media (min-width: 768px) {
  .contact-form .form__field {
    width: 45%;
  }
}
.contact-form .form__input {
  margin-bottom: 2rem;
}
.contact-form .form__submit {
  margin: 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .contact-form .form__submit {
    width: auto;
    align-self: flex-end;
  }
}

.about {
  display: flex;
  flex-direction: column;
  margin: 5rem;
  margin-top: 0;
}
.about__title {
  text-align: center;
  border-bottom: 1px solid #000;
  font-size: 3.5rem;
  font-weight: 700;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .about__title {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .about__info {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.about__info img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .about__description {
    text-align: left;
  }
}
.about__description .description__title {
  font-weight: 700;
}
.about__description p {
  margin: 1.5rem;
}

.choose {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
.choose__title {
  text-align: center;
  font-weight: 700;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .choose__icons {
    display: flex;
    justify-content: center;
    gap: 5rem;
  }
}
.choose__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.choose__info i {
  font-size: 5rem;
}

.policies {
  display: flex;
  flex-direction: column;
  margin: 5rem;
  margin-top: 0;
}
.policies__title {
  text-align: center;
  border-bottom: 1px solid #000;
  font-size: 3.5rem;
  font-weight: 700;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .policies__title {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .policies__info {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.policies__info img {
  width: auto;
  height: auto;
  margin-left: 50px;
  margin-top: 150px;
}
.policies__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .policies__description {
    text-align: left;
  }
}
.policies__description .description__title {
  font-weight: 700;
}
.policies__description p {
  margin: 1.5rem;
}

/* styles.css */
.cart {
  width: 80%;
  border-collapse: collapse;
  margin: 0 auto;
}

.cart th, .cart td {
  text-align: center;
  padding: 10px;
  border: 1px solid #ccc;
}

.cart tbody tr {
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  height: 100px;
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin: 20px;
}

.button {
  padding: 10px 20px;
  background-color: #ffffff;
  color: black;
  border: 2px solid black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
}

.box {
  flex: 0 0 250px; /* Width of the right box */
  padding: 20px;
  border: 2px solid black;
  height: 50px;
  width: 40%;
  background-color: #ffffff;
  margin: 0 auto;
  justify-content: center;
}

.red-button {
  color: #FFFFFF;
  background-color: #FF0000;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
}

.square-container {
  background-color: #fff;
  border: 2px solid #ccc;
  width: 700px;
  height: 400px;
  margin-left: 50px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  overflow: auto;
}

.scrollable-content {
  overflow: auto;
}

.input-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
}

.input-container label {
  text-align: left;
}

.input-container input {
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  width: 350px;
  text-align: left;
}

.input-container input::placeholder {
  text-align: left;
}

.search-header {
  text-align: left;
}

.search-button {
  width: 100px;
  height: 50px;
  border: none;
  border-radius: 20px;
  background-color: #bbbbbb;
  color: #000000;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.result-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.result-list li {
  margin-bottom: 25px;
}

.line {
  border-bottom: 1px solid black;
}

.form-title {
  font-size: 3rem;
  margin: 0;
}

.form-description {
  font-size: 2rem;
  margin: 1.5rem 0;
}

.form {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form__field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
  width: 100%;
}
.form__label {
  font-size: 1.5rem;
}
.form__input, .form__textarea {
  background-color: #F5F5F5;
  color: #000000;
  border: none;
  padding: 1.5rem;
  border-radius: 1rem;
  font-size: 1.5rem;
}
.form__input-login {
  padding: 1rem 0;
  border: none;
  border-bottom: 1px solid #000000;
}
.form__submit {
  background-color: #DB4444;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-right: 1.5rem;
  border-radius: 0.5rem;
  align-self: center;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
.form__submit:hover {
  background-color: #c62626;
  cursor: pointer;
}
.form__submit-100 {
  background-color: #DB4444;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-right: 1.5rem;
  border-radius: 0.5rem;
  align-self: center;
}
@media (min-width: 768px) {
  .form__submit-100 {
    width: auto;
  }
}
.form__submit-100:hover {
  background-color: #c62626;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__submit-100 {
    width: 100%;
  }
}
.form__textarea {
  width: 100%;
  height: 20rem;
}

.action {
  margin-top: 1rem;
  text-align: center;
  color: #000000;
}
.action span {
  text-decoration: underline;
  color: #DB4444;
}

.actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.actions .action {
  color: #000000;
  font-size: 1.5rem;
}
.actions .action span {
  text-decoration: underline;
  color: #DB4444;
}
.actions .action-accent {
  color: #DB4444;
  font-size: 1.5rem;
}